import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			rememberPass: true,
			userPhone: '',
			userPass1: '',
			userPass2: '',
			imgCode: '',
			ImgRandom: '',
			imgCodeSrc: '',
			phoneCode : '',
			recCode: '',
			codeTime: '获取'
		};
	},
	onLoad(){
		this.getImgCode();
	},
	methods:{
		goRegistered(){
			var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
			if (!myreg.test(this.userPhone)) {
				uni.showToast({
					position: 'bottom',
					title: '手机格式不正确',
					icon: 'none',
					duration: 2500
				});
				return;
			}
			if (this.userPass1 == '' || this.userPass1 != this.userPass2 || this.userPass1.length < 8 || this.userPass1.length > 24) {
				uni.showToast({
					position: 'bottom',
					title: '密码格式不正确',
					icon: 'none',
					duration: 2500
				});
				return;
			}
			if(!this.rememberPass){
				uni.showToast({
					position: 'bottom',
					title: '请同意《注册协议》《交易服务协议》后再继续',
					icon: 'none',
					duration: 2500
				});
				return;
			}
			if(this.imgCode == ''){
				uni.showToast({
					position: 'bottom',
					title: '图片验证码不能为空',
					icon: 'none',
					duration: 2500
				});
				return;
			}
			if(this.phoneCode == ''){
				uni.showToast({
					title: '短信验证码不能为空',
					icon: 'none',
					duration: 2500
				})
				return;
			}
			uni.showLoading({
				mask : true,
				title : '注册中~',
				
			})
			http.request({
				url:'member/registered',
				method:'GET',
				data:{
					phone:this.userPhone,
					loginPwd:this.userPass1,
					upCode:this.recCode,
					phoneCode:this.phoneCode,
					imgCode:this.imgCode
				},
				isRe : this.ImgRandom
			}).then((res)=>{
				if(res.data.success){
					uni.navigateTo({
						url : '/pages/checkpoint/login/login?msg=注册成功'
					})
				}
			})
		},
		//获取图片验证码
		getImgCode() {
			//手机唯一识别码
			this.ImgRandom = '';




			this.imgCodeSrc = this.$store.state.webPath + 'member/generateImage?identifier=' + this.ImgRandom + '&random=' + Math.random();
		},
		queryAgree(){
			uni.navigateTo({
				url: '/pages/main/publicView/news/news?title=注册协议&type=topBar&barType=1010'
			});
			
		},
		getPhoneCode(){
			if(!(/^1[3456789]\d{9}$/.test(this.userPhone))){ 
				uni.showToast({
					title: '手机号码输入有误',
					icon: 'none',
					duration: 2500
				})
				return; 
			}
			if(this.codeTime != '获取'){
				return;
			}
			uni.showLoading({
				title : '发送中',
				mask:true
			})
			http.get('member/getPhoneCode',{phone:this.userPhone}).then((res)=>{
				uni.showToast({
					title: '验证码发送成功',
					icon: 'none',
					duration: 2500
				})
				let _this = this;
				let t = 60;
				_this.codeTime = '60s';
				let codeSet = setInterval(()=>{
					t--;
					_this.codeTime = t + 's';
					if(t == 1){
						_this.codeTime = '获取';
						clearInterval(codeSet)
					}
				},1000)
				
			})
		}
	}
}
